import modelExtend from 'dva-model-extend'
import { queryComplaint } from '../../services/users'
import { pageModel } from '../../models/common'
import queryString from 'query-string'

interface Istate {
  currentItem: any,
  modalVisible: boolean,
}

export default modelExtend(pageModel, {
  namespace: 'complaint',
  state: {
    currentItem: {},
    modalVisible: false,
  },
  subscriptions: {
    setup ({ dispatch, history }: any) {
      history.listen((location: any) => {
        if (location.pathname === '/users/complaint') {
          dispatch({
            type: 'query',
            payload: {
              // status: 2,
              ...queryString.parse(location.search),
            },
          })
        }
      })
    },
  },
  effects: {
    * query ({ payload }: any, { call, put, select }: any) {
      const searchValue = yield select((state: any) => state.complaint.searchValue)
      const data = yield call(queryComplaint, { ...payload, target_type: 4, name: searchValue })
      if (data.success) {
        const { complaint } = data.data
        yield put({
          type: 'querySuccess',
          payload: {
            list: complaint.data,
            pagination: {
              current: Number(payload.current_page) || 1,
              pageSize: Number(payload.per_page) || 10,
              total: complaint.total,
            },
          },
        })
      } else {
        throw data
      }
    },
  },
  reducers: {
    updateState (state: Istate, { payload }: any) {
      return {
        ...state,
        ...payload,
      }
    },
  },
})



